import React, {useState} from 'react';
import '../../style/App.css';

let PeopleMenu = props => {
    let reName = (action) => {
        props.itsRename(action.target.name, action.target.value);
    };
    return (
        <div>
            <span><input value={props.nickname}
                         name={"NICKNAME"}
                         onChange={reName}
                         className={'width'}
                         placeholder={'NAME через пробел если это несколько слов'}/>
            </span>
            <span><input value={props.listTags}
                         name={"TAGS"}
                         onChange={reName}
                         className={'width'}
                         placeholder={"TAGS через пробел"}/>
            </span>
            <img src={'https://1текстпесни.рф/uploads/images/t/o/j/toj_olsun_bajram_olsun_svadebnaja_2011.jpg'}
                 onClick={() => props.itsRename('DOUBLE')} className={'img-but'}/>
        </div>
    )
};
export default PeopleMenu;



